import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { TableData } from '../md/md-table/md-table.component';
import { LegendItem, ChartType } from '../md/md-chart/md-chart.component';
import { CommonService } from "../services/commonService";
import { WorkOrderService } from "../services/workOrderService";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import * as Chartist from 'chartist';
import { } from 'googlemaps';
import { MapsAPILoader } from '@agm/core';
import { DatePipe } from '@angular/common';
import { Http, Response, RequestOptions, Headers } from "@angular/http";

declare const $: any;

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    providers: [CommonService, WorkOrderService, DatePipe],
})
export class DashboardComponent implements OnInit, AfterViewInit {
    public latitude: number;
    public longitude: number;
    public zoom: number;
    public jobMapList: any;
    public userProfile: any;
    public latlongList: any[] = [];

    public dashJPage = new FormGroup({
        jobStatus: new FormControl(),
        dateType: new FormControl(),
        jobDate: new FormControl()
    });

    public dashMPage = new FormGroup({
        mtStatus: new FormControl(),
        mtType: new FormControl(),
        mapDate: new FormControl()
    });

    constructor(
        private formBuilder: FormBuilder,
        private commonService: CommonService,
        private workOrderService: WorkOrderService,
        public datepipe: DatePipe,
        private router: Router,
        private http: Http
    ) {
    }

    public ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));
            this.latitude = -33.865143
            this.longitude = 151.209900;
            this.zoom = 10;
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }
    ngAfterViewInit() {
    }

    getJobMapForDashboard() {
        $('.loader').show();
        let jobDate = null;
        if (this.dashJPage.controls["jobDate"].value != null) {
            jobDate = this.datepipe.transform(this.dashJPage.controls["jobDate"].value, 'yyyy/MM/dd HH:mm:ss');
        }
        var contact = {
            UserID: this.userProfile.ID,
            Status: this.dashJPage.controls["jobStatus"].value,
            FromDate: jobDate,
            ToDate: null
        };
        this.workOrderService.GetJobMapForDashboard(this.commonService.baseApiUrl, contact).subscribe(
            data => {
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                this.jobMapList = null;
                this.latlongList = [];
                if (userInfo != null) {
                    console.log(userInfo);
                    this.jobMapList = userInfo;
                    $('.loader').hide();
                    for (let mapData of this.jobMapList) {
                        this.getLocation(mapData.AddressLine);
                    }
                }
            });
    }

    getLocation(address: any) {
        this.latitude = null;
        this.longitude = null;
        if (address != null) {
            this.http.get("https://maps.google.com/maps/api/geocode/json?address=" + address + "&key=AIzaSyDvC4D3B6FJvtMZLOfUsIUnpUxpET_uc-w").subscribe(
                data => {
                    var dataMap = JSON.stringify(data);
                    var mapParse = JSON.parse(dataMap);
                    var mapList = JSON.parse(mapParse["_body"]);
                    if (mapList != null) {
                        this.latitude = mapList["results"][0]["geometry"]["location"]["lat"];
                        this.longitude = mapList["results"][0]["geometry"]["location"]["lng"];
                        this.zoom = 10;
                        var datalist = {
                            "latitude": this.latitude,
                            "longitude": this.longitude
                        }
                        this.latlongList.push(datalist);
                    }
                }
            );
        }
    }
}



